let noopHooks = function(data, next) {
  next();
};
export default {
  inheritAttrs: false,
  inject: {
    $parentView: {
      default: null
    },
    $frame: {
      default: null
    },
    $dialog: {
      default: null
    }
  },
  provide() {
    return {
      $vm: this,
      $parentView: this,
      $choiceParams: this.choiceParams
    }
  },
  props: {
    nameSpace: {
      type: String,
      default: ""
    },
    pageName: {
      type: String,
      default: ""
    },
    queryString: {
      type: Object,
      default () {
        return {};
      }
    },
    choiceParams: {
      type: Object,
      default () {
        return null;
      }
    }
  },
  data() {
    return {
      isDestroyed: false,
      isReady: false,
      context: {
        $$viewInitData: {},
        $$viewData: {},
        $$enum: {}
      },
      frames: {},
      isLoaded: false,
      isLayoutView: true,
      $choice: null,
      metadataDict: {},
      loading: false,
      isView: false,
      flows: {}
    };
  },
  computed: {
    page() {
      return this.$refs.page;
    },
    $dialogArgs() {
      try {
        if (window.opener && window.opener.dialogArgs) {
          return window.opener.dialogArgs || null;
        }
      } catch (ex) {
        return null;
      }
      return null;
    },
    $viewData() {
      return this.context.$$viewData;
    },
    $viewInitData() {
      return this.context.$$viewInitData;
    },
    $enum() {
      return this.context.$$enum;
    },
    $dataKey() {
      let params = this.queryString;
      return params ? params.id : "";
    }
  },
  beforeCreate() {},
  created() {
    this.isView = this.queryString.isview;
    if (this.$frame) {
      this.$frame.content = this;
    }
    // this.$on("datachoice.addControl", (evt) => {
    //   //将可以选择数据的组件存放到页面的$choice中
    //   if (evt.caption && !this.$data.$choice) {
    //     this.$data.$choice = evt;
    //   }
    // });
  },
  updated() {
    // console.log("开始创建页面")
  },
  mounted() {
    this.init();
    // console.log(this.$eval("'weng'+@[url('id')]"))
  },
  beforeDestroy() {
    // console.log("销毁")
    this.isDestroyed = true;
    this.context.$$viewData = null;
    this.context.$$viewInitData = null;
    this.$data.$choice = null;
    for (let k in this.$frames) {
      this.$delete(this.frames, k);
    }
    if (this.$frame) {
      delete this.$frame.content;
    }

  },
  methods: {
    //初始化页面
    init() {
      // let loading = dsf.layer.pc.loading();
      new Promise(resolve => { resolve(); })
        .then(() => {
          if (!this.isDestroyed) {
            //获取页面初始化数据
            return this.initData();
          }
        })
        .then(response => {
          if (!this.isDestroyed) {
            //执行初始化数据完成的钩子函数
            let inited = this.$options.inited || noopHooks;
            return promiseFn(inited).call(this, response ? response.data : {});
          }
        })
        .then(data => {
          if (!this.isDestroyed) {
            let _this = this;
            _this.context.$$viewInitData = data;
            initEnum.call(this);
            this.isLoaded = true;
            this.$nextTick(() => {
              //调用布局resize
              dsf.resizeComponent(this.page);
            });
            if (_this.loadData) {
              //如果存在主键参数则加载表单数据
              return _this.loadData(this.nameSpace);
            }
          }
        })
        .then(response => {
          if (!this.isDestroyed) {
            //执行查询表单数据完成的钩子
            let dataLoaded = this.$options.dataLoaded || noopHooks;
            return promiseFn(dataLoaded).call(this, response ? response.data : {});
          }
        })
        .then(data => {
          if (!this.isDestroyed) {
            let _this = this;
            dsf.log("开始为表单进行赋值了");
            let viewData = initControls.call(this);
            if (data) {
              dsf.mix(viewData, data);
            }
            _this.context.$$viewData = viewData;
          }
        })
        .catch(err => {
          if (err && err.message) {
            dsf.layer.pc.message(err.message, false);
          }
        })
        .finally(() => {
          this.$nextTick(() => {
            //重置页面和下级组件的宽高
            let ready = this.$options.ready;
            ready && ready.call(this);
            this.isReady = true;
          });
        });
    },
    //调用初始化接口
    initData() {
      return new Promise((resolve, reject) => {
        let params = dsf.mix({
            pageName: this.pageName,
            namespace: this.nameSpace
          },
          this.queryString
        );
        this.$http.get("/meta/init", params)
          .done(data => {
            resolve(data);
          })
          .error(err => {
            reject({ message: err.message });
          });
      });
    },
    $proxyFn() {
      let args = Array.prototype.slice.call(arguments, 1);
      let fnName = arguments[0];
      this[fnName] && this[fnName].apply(this, args);
    },
    $clone(obj) {
      return JSON.parse(JSON.stringify(obj))
    },
    //子表或者列表在循环输出单元格时对属性进行复制并传递给updateControlPropsInTemplate函数
    updateControlPropsInTemplate(designId, scope) {
      let key = designId + "-" + scope.row.$hash;
      let props = null;
      if (!this.$controls[key]) {
        props = this.$clone(this.$controls[designId])
        this.$set(this.$controls, key, props);
        if (props.metadata_fullcode) {
          let mkey = props.metadata_fullcode;
          //赋默认值
          if (this.$viewInitData[mkey] && dsf.isDef(this.$viewInitData[mkey].defaultValue)) {
            if (!dsf.hasOwn(scope.row, mkey)) {
              this.$set(scope.row, mkey, this.$viewInitData[mkey].defaultValue);
            }
          }
        }
      } else {
        props = this.$controls[key];
      }
      if (scope._self.updateControlPropsInTemplate) {
        props = scope._self.updateControlPropsInTemplate(props, scope);
      }
      if (this.$updateControlPropsInTemplate) {
        props = this.$updateControlPropsInTemplate(props, scope);
      }
      return props;
    },
    $updateControlPropsInTemplate(ctrlProps, scope) {
      return ctrlProps;
    },
    reset() {
      for (let k in this.$refs) {
        if (this.$refs[k] && this.$refs[k].reset) {
          this.$refs[k].reset();
        }
      }
    },
    reloadData() {
      for (let k in this.$refs) {
        if (this.$refs[k] && this.$refs[k].reloadData) {
          this.$refs[k].reloadData();
        }
      }
    },
    //返回$choice中组件的选中对象
    yes() {
      let result = this.$data.$choice.getReturnValue();
      return result;
    },
    //获取所在作用域的元数据字典
    getScopeMetaDataDict(component) {
      if (dsf.isEmptyObject(this.metadataDict)) {
        return {};
      }
      let metadata = component.metadata;
      if (!metadata || !component.metadata_fullcode) {
        return {};
      }
      let fullCode = component.metadata_fullcode;
      if (this.metadataDict[fullCode]) {
        if (this.metadataDict[fullCode].inSubTable) {
          let result = {};
          _.each(this.metadataDict, (el, i) => {
            if (el.inSubTable === this.metadataDict[fullCode].inSubTable) {
              result[i] = el;
            }
          });
          return result;
        } else {
          let result = {}
          _.each(this.metadataDict, (el, i) => {
            if (dsf.isUnDef(el.inSubTable)) {
              result[i] = el;
            }
          });
          return result;
        }
      }
    }
  }
};

//将方法转换为promise方法
function promiseFn(fn) {
  return function() {
    let _array = Array.prototype.slice.call(arguments);
    let self = this;
    return new Promise((resolve, reject) => {
      let next = mark => {
        if (mark === false) {
          reject.apply(null, _array);
        } else {
          resolve.apply(null, _array);
        }
      };
      fn.apply(self, _array.concat([next]));
    });
  };
}

function initEnum() {
  for (let k in this.context.$$viewInitData) {
    let d = this.context.$$viewInitData[k];
    if (d && d.dataSource) {
      this.$set(this.context.$$enum, k, d.dataSource);
    }
  }
}

function initControls() {
  let vd = {};
  let controls = this.$controls;
  let initData = this.$viewInitData;
  for (let k in controls) {
    let ctrl = controls[k];
    //如果isview=1页面只读
    if (ctrl.isFormControl && this.queryString.isview == 1) {
      ctrl.readOnly = true;
    }
    if (ctrl.metadata) {
      let key = ctrl.metadata_fullcode;
      if (ctrl.metadata.level == 0) {
        if (initData[key]) {
          //将默认值赋予viewData
          if (dsf.isDef(initData[key].defaultValue)) {
            vd[ctrl.metadata_fullcode] = initData[key].defaultValue;
          }
          // else {
          //   vd[ctrl.metadata_fullcode] = null;
          // }
          // //是否存在枚举数据源
          // if (dsf.isDef(initData[key].dataSource)) {
          //   ctrl.items = initData[key].dataSource;
          // }
        }
      } else if (ctrl.metadata.level == 1 && ctrl.isOneToMany) {
        vd[ctrl.metadata_fullcode] = [];
      }
    }
  }
  return vd;
}